export function computeIssuesForCategory(category: string, catalog: any) {
  const subtypes = catalog[category] || {};
  const issueSet = new Set<string>();

  Object.values(subtypes).forEach((subtypeObj: any) => {
    if (Array.isArray(subtypeObj.problems)) {
      subtypeObj.problems.forEach((p: string) => issueSet.add(p.trim()));
    }
  });

  return Array.from(issueSet).sort();
}
