"use client";
import { useEffect, useState } from "react";
import Chip from "@/components/Chip";
import CategoryCard from "@/components/CategoryCard";
import { fetchCatalog } from "@/app/api/backend";
import clsx from "clsx";

export default function QueryBuilderPage() {
  const [catalog, setCatalog] = useState<any | null>(null);
  const [selectedCategories, setSelectedCategories] = useState<string[]>([]);
  const [categoryStates, setCategoryStates] = useState<Record<string, any>>({});
  const [centered, setCentered] = useState(true);

  useEffect(()=>{ fetchCatalog().then(setCatalog); }, []);

  if (!catalog) return <div className="min-h-screen flex items-center justify-center">Loading…</div>;

  const categories = Object.keys(catalog);

  const toggleCategory = (cat: string) => {
    setSelectedCategories(prev => {
      const next = prev.includes(cat) ? prev.filter(x=>x!==cat) : [...prev, cat];
      // initialize category state if selected
      if (!prev.includes(cat)) setCategoryStates(cs => ({...cs, [cat]: { issues: [], subtypes: [], speed:"", notes:"" }}));
      else setCategoryStates(cs => { const n = {...cs}; delete n[cat]; return n; });
      return next;
    });
  };

  const handleCategoryChange = (cat: string, state: any) => {
    setCategoryStates(prev => ({ ...prev, [cat]: state }));
  };

  const handleSubmitAll = async () => {
    // build final combined sentence (Option B)
    const sentences = selectedCategories.map(cat => {
      const s = categoryStates[cat];
      if (!s || !s.issues?.length || !s.subtypes?.length) return null;
      const iss = s.issues.join(" & ");
      const subs = s.subtypes.join(" | ");
      const speed = s.speed ? ` on ${s.speed} road` : "";
      return `${iss} ${subs}${speed}.`;
    }).filter(Boolean);
    const finalPrompt = sentences.join(" ");
    // send to backend (use your processQuery)
    console.log("Final prompt:", finalPrompt);
    // call process endpoint...
  };

  return (
    <div className="min-h-screen flex items-start md:items-center justify-center py-12">
      <div className="w-full max-w-3xl">
        <header className="text-center mb-6">
          <h1 className="text-2xl font-bold">RoadMarshal AI</h1>
          <p className="text-sm text-zinc-500 mt-2">Hi — how can I assist you today? Select your query type(s) below.</p>
        </header>

        {/* Category chips */}
        <div className="mb-6 flex flex-wrap gap-3 justify-center">
          {categories.map(cat => (
            <Chip key={cat} label={cat} selected={selectedCategories.includes(cat)} onClick={()=>toggleCategory(cat)} />
          ))}
        </div>

        {/* Category cards (progressive, collapse completed ones) */}
        <div className="space-y-6">
          {selectedCategories.map(cat => (
            <CategoryCard key={cat} category={cat} catalog={catalog} onChange={(st:any)=>handleCategoryChange(cat, st)} initial={categoryStates[cat]} />
          ))}
        </div>

        {/* final actions */}
        <div className="mt-8 flex justify-center gap-3">
          <button className="px-6 py-3 bg-zinc-100 dark:bg-zinc-800 border rounded" onClick={()=>{ /* edit */ }}>Edit</button>
          <button className="px-6 py-3 bg-emerald-600 text-white rounded" onClick={handleSubmitAll}>Submit All</button>
        </div>
      </div>
    </div>
  );
}
